html {
    box-sizing: border-box;
}

*, *::after, *::before {
    box-sizing: inherit;
}
body{
    font-family: 'Roboto Slab', serif;
    font-size: clamp(0.75rem,calc(0.5rem+1vw),1.25rem);
    margin: 0;

}
a {
    color: inherit;
    text-decoration: none;

}
h1,h2,h3,h4,h5{
     font-family: 'Roboto', sans-serif; 
}